#Product-page-part{
  width: 100%;
  min-width: 1226px;
  background-color: #f5f5f5;
  padding: 4px 0px 12px;
}

.Home-flash-sale {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  position: relative;
  width: 1226px;
  padding-bottom: 22px;
  margin:auto ;
}

.xiaoMi-shangou {
  flex-basis: 100%;
  height: 58px;
  line-height: 58px;
}
h2 {
  margin: 0px;
  font-size: 22px;
  font-weight: 200;
}

/*计时*/

.flashsale-countdown {
  width: 234px;
  height: 340px;
  background-color: aqua;
  text-align: center;
  background: #f1eded;
}

.flashsale-countdown::before{
  position: absolute;
  content: "";
  background: palevioletred;
  top: 58px;
  left: 0px;
  width: 234px;
  height: 1px;
}

.flashsale-countdown img {
  margin: 25px auto;
}

.round {
  font-size: 21px;
  color: #ef3a3b;
  padding-top: 55px;
}

.countDown {
  display: inline-flex;
  width: 168px;
  margin: 28px auto 0px;
}

.countDown span{
  width: 46px;
  height: 46px;
  background-color: #605751;
  color: #fff;
  font-size: 24px;
  line-height: 46px;
}

.countDown li {
  width: 15px;
  height: 46px;
  line-height: 46px;
  color: #605751;
  font-size: 28px;
}

/*小米闪购商品框*/
.caseWrapper {
  margin-left: 234px;
  width: 992px;
  position: absolute;
  bottom: 22px;
  overflow: hidden;
  display: inline-flex;
}

.content {
  width: 234px;
  height: 300px;
  padding-top: 39px;
  margin-left: 14px;
  text-align: center;
  background-color: rgba(255, 254, 254, 1);
  position: relative;
}
.content::before {
  position: absolute;
  content: "";
  background:rgba(0,0,0,.02);
  top: 0px;
  left: 0px;
  width: 234px;
  height: 1px;
}
.content:nth-of-type(3n)::before{
  background: wheat;
}
.content:nth-of-type(3n+1)::before {
  background: greenyellow;
}
.content:nth-of-type(3n+2)::before {
  background: plum;
}


.thumb {
  width: 160px;
  height: 166px;
  margin: 0 auto 22px;
  background-color: rgba(255, 254, 254, 1);
}

.thumb img {
  width: 160px;
  height: 160px;
}

.title {
  width: 194px;
  margin: 0 20px 3px;
  font-size: 14px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}


.desc {
  width: 194px;
  font-size: 12px;
  margin: 0 20px 12px;
  color: #b0b0b0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.price {
  width: 194px;
  margin: 0 20px;
  color: #ff6700;
}

.price del {
  color: #b0b0b0;
}

/*bana box*/
.Home-banner-box {
  width: 1226px;
  margin: 0px auto 16px;
}
.Home-banner-img {
  height: 120px;
  width: 1226px;
}

/*手机*/
.Home-product-sale {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  position: relative;
  width: 1226px;
  padding-bottom: 36px;
  margin:auto ;
}

.Home-phone-mainImg, .Home-phone-mainImg img{
  width: 234px;
  height: 614px;
}

.Home-phone-mainImg:hover  {
  position: relative;
  top: -2px;
  box-shadow: -5px 5px 30px #b0b0b0;
  transition: all .5s;
}

.product-case-wrapper {
  margin-left: 234px;
  width: 992px;
  position: absolute;
  bottom: 22px;
  display: flex;
  flex-wrap: wrap;
}

.product-content {
  width: 234px;
  height: 280px;
  padding-top: 20px;
  margin: 0px 0 14px 14px;
  text-align: center;
  background-color: rgba(255, 254, 254, 1);
  position: relative;
}

.product-content:hover  {
  position: relative;
  top: -2px;
  box-shadow: -5px 5px 20px #b0b0b0;
  transition: all .5s;
}

.product-thumb {
  width: 160px;
  height: 166px;
  margin: 0 auto 12px;
  background-color: rgba(255, 254, 254, 1);
}

.product-thumb img {
  width: 160px;
  height: 160px;
}

.product-title {
  width: 214px;
  margin: 0 10px 2px;
  font-size: 14px;
  font-weight: 400;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.product-desc {
  width: 214px;
  font-size: 12px;
  margin: 0 10px 10px;
  color: #b0b0b0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.product-price {
  width: 214px;
  margin: 0 10px 20px;

}

.product-price span {
  color: #ff6700;
}

.product-price del {
  color: #b0b0b0;
}

/*家电*/
.Home-appliance-mainImg-box {
  width: 234px;
  height: 614px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.Home-appliance-mainImg {
  width: 234px;
  height: 300px;
}

.Home-appliance-mainImg:hover,.Home-appliance-more:hover {
  position: relative;
  top: -2px;
  box-shadow: -5px 5px 20px #b0b0b0;
  transition: all .5s;
}

.Appliance-more-box {
  width: 234px;
  height: 300px;
  position: absolute;
  right: 0;
  bottom: 36px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.Home-appliance-more {
  width: 234px;
  height: 103px;
  padding-top: 40px;
  background-color: rgba(255, 254, 254, 1);
  display: flex;
}

.Home-appliance-more-desc {
  margin-left: 30px;
}

.Home-appliance-more-desc h3,.Home-appliance-more-desc p {
  width: 94px;
  margin: 0px;
}

.Home-appliance-more-desc>p {
  font-size: 14px;
  margin-top: 5px;
  height: 21px;
  color: #ff6700;
}

.Home-appliance-more-desc h3 {
  font-size: 14px;
  font-weight: 400;
  white-space: wrap;
}

.Home-appliance-more-img {
  width: 80px;
  height: 80px;
  margin-left: 10px;
  position: relative;
  top: -8px;
}

.Home-appliance-more-img img {
  width: 80px;
  height: 80px;
}

.for-more-desc {
  width: 94px;
  height: 45px;
  margin-left: 30px;
  margin-top: 10px;
}

.for-more-desc p {
  margin: 0;
  font-size: 18px;
}

.for-more-desc small {
  font-size: 12px;
  color:#757575;
  position: relative;
  top: -2px
}

.forMoreImg {
  width: 48px;
  height: 48px;
  margin-left: 20px;
  position: relative;
  top: 10px;
  left: 10px;
}

.disappear {
  display: none;
}